<template>
  <div class="star" :class="starType">
    <span v-for="itemClass in itemClasses()" :class="itemClass" class="star-item"></span>
  </div>
</template>

<script>
  const LENGTH = 5;
  const CLS_ON = "on";
  const CLS_HALF = "half";
  const CLS_OFF = "off";

  export  default  {
//    接收父组件传过来的值
    props: {
//      图片类型
      size: {
        type: Number
      },
//      评价分数
      score: {
        type:Number
      }
    },
//    计算方法
    computed: {
//      拼接类名
      starType(){
        return 'star-' + this.size;
      }
    },
    methods: {
      itemClasses : function () {
        let result = [];
//        进行四舍五入计算
        let score = Math.floor(this.score * 2) / 2;
//        判断四舍五入之后是否带有小数
        let hasDecimal = score % 1 !== 0;
//        取评价的整值
        let integer = Math.floor(score);
//        遍历评价整值的星
        for (let i = 0; i < integer; i++) {
          result.push(CLS_ON);
        }
//        如果存在小数，就添加一颗半星
        if (hasDecimal) {
          result.push(CLS_HALF);
        }
//        遍历剩余的位子为没星
        while (result.length<LENGTH) {
          result.push(CLS_OFF);
        }
        return result;
      },
    }
  }
</script>

<style lang="less">
  @import "../../common/style/mixin";
  .star {
    font-size: 0;
    &.star-48{
      .star-item {
        width: 20px;
        height: 20px;
        margin-right: 22px;
        -webkit-background-size: 20px;
        background-size: 20px;
        &:last-child {
          margin-right: 0;
        }
        &.on {
          .bg-image("../../components/star/star48_on")
        }
        &.half {
          .bg-image("../../components/star/star48_half")
        }
        &.off {
          .bg-image("../../components/star/star48_off")
        }
      }
    }
    &.star-36{
      .star-item {
        width: 15px;
        height: 15px;
        margin-right: 6px;
        -webkit-background-size: 15px;
        background-size: 20px;
        &:last-child {
          margin-right: 0;
        }
        &.on {
          .bg-image("../../components/star/star36_on")
        }
        &.half {
          .bg-image("../../components/star/star36_half")
        }
        &.off {
          .bg-image("../../components/star/star36_off")
        }
      }
    }
    &.star-24{
      .star-item {
        width: 10px;
        height: 10px;
        margin-right: 3px;
        -webkit-background-size: 10px;
        background-size: 20px;
        &:last-child {
          margin-right: 0;
        }
        &.on {
          .bg-image("../../components/star/star48_on")
        }
        &.half {
          .bg-image("../../components/star/star48_half")
        }
        &.off {
          .bg-image("../../components/star/star48_off")
        }
      }
    }
    .star-item {
      display: inline-block;
      background-repeat: no-repeat;
    }
  }
</style>



