<template>
  <transition name="move">
    <div v-show="showFlag" class="food">
      <div class="food-content">
        <div class="image-header">
          <img :src="food.image">
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
    export default {
      props : {
        food : {
          type : Object
        }
      },
      data() {
        return {
          showFlag : false
        }
      },
      methods : {
        show() {
          this.showFlag = true;
        }
      }
    }
</script>

<style lang="less">
  .food {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 48px;
    z-index: 30;
    width: 100%;
    background-color: #fff;
    transition: all .2s linear;
    &.move-enter-active,&.move-leave-active {
      transform: translate3d(0,0,0);
    }
    &.move-enter,&.move-lever-active {
      transform: translate3d(100%,0,0);
    }
  }
</style>



