<!--购物车模版-->

<template>
  <div>
    <div class="shopcart">
      <div class="content" @click="toggleList">
        <div class="content-left">
          <div class="logo-wrapper">
            <div class="logo" :class="{highlight : totalCount > 0}">
              <i class="icon-shopping_cart" :class="{highlight : totalCount > 0}"></i>
            </div>
            <div class="num" v-show="totalCount > 0">{{totalCount}}</div>
          </div>
          <div class="price" :class="{highlight : totalPrice > 0}">￥{{totalPrice}}</div>
          <div class="desc" >另需配送费￥{{deliveryPrice}}元</div>
        </div>
        <!--禁止冒泡和默认事件-->
        <div class="content-right" @click.stop.prevent="pay">
          <div class="pay" :class="payClass">
            {{payDesc}}
          </div>
        </div>
      </div>
      <transition name="drop">
        <div class="ball-container">
          <div v-for="ball in balls" v-show="ball.show" class="ball"></div>
          <div class="inner inner-hook"></div>
        </div>
      </transition>
      <transition name="fold">
        <div class="shopcart-list" v-show="listShow">
          <div class="list-header">
            <h1 class="title">购物车</h1>
            <span class="empty" @click="empty">清空</span>
          </div>
          <div class="list-content" ref="listContent">
            <ul>
              <li class="food" v-for="food in selectFoods">
                <span class="name">{{food.name}}</span>
                <div class="price">
                  <span>￥{{food.price*food.count}}</span>
                </div>
                <div class="cartcontrol-wrapper">
                  <cartcontrol :food="food"></cartcontrol>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </transition>
    </div>
    <transition name="fade">
    <div class="list-mask" v-show="listShow" @click="hideList"></div>
    </transition>
  </div>
</template>

<script>
  import BScroll from "better-scroll";
  import cartcontrol from "../cartcontrol/cartcontroLvue.vue";
  export default {
      data () {
        return {
          balls: [
            {
              show : false
            },
            {
              show : false
            },
            {
              show : false
            },
            {
              show : false
            },
            {
              show : false
            },
          ],
          dropBalls : [],
          fold: false,
        }
      },
      props : {
//        父组件传价格和数量
        selectFoods : {
          type : Array,
          default () {
            return [
              {
//
              }
            ]
          }
        },
//        最低配送费
        deliveryPrice : {
          type : Number,
        },
//        最低起送
        minPrice : {
          type : Number
        }
      },
      computed : {
//        价格总数函数
        totalPrice() {
          let total = 0;
          this.selectFoods.forEach(function (food) {
            total += food.price * food.count;
          });
          return total;
        },
//        购物车数量函数
        totalCount() {
          let count = 0;
          this.selectFoods.forEach(function (food) {
            count += food.count;
          });
          return count;
        },
//        判断购物车价格对应的三种情况
        payDesc() {
          if(this.totalPrice === 0) {
            return ` ￥${this.minPrice}元起送`;
          }else if (this.totalPrice < this.minPrice) {
            let diff = this.minPrice - this.totalPrice;
            return `还差￥${diff}元起送`;
          }else {
            return '去结算';
          }
        },
//        判断购物价格是否达到起步配送
        payClass() {
          if(this.totalPrice < this.minPrice) {
            return "not-enough";
          }else {
            return "enough";
          }
        },
        listShow () {
          if(!this.totalCount) {
            this.fold = true;
            return false;
          }
          let show = !this.fold;
          if (show) {
            this.$nextTick(() => {
              if (!this.scroll) {
                this.scroll = new BScroll(this.$refs.listContent, {click: true});
              } else {
                this.scroll.refresh();
              }
            })
          }
          return show;
        }
      },
      methods: {
        pay () {
          if(this.totalPrice < this.minPrice) {
            return;
          }
          window.alert(`支付${this.totalPrice}元`);
        },
        drop(el) {
          for (let i = 0; i < this.balls.length; i++) {
            let ball = this.balls[i];
            if (!ball.show) {
              ball.show = true;
              ball.el = el;
              this.dropBalls.push(ball);
              return;
            }
          }
        },
        toggleList() {
          if (!this.totalCount) {
            return;
          }
          this.fold = !this.fold;
        },
        transition: {
          drop: {
            beforeEnter(el) {
              let count = this.balls.length;
              while (count--) {
                let ball = this.balls[count];
                if(ball.show) {
                  let rect = ball.el.getBoundingClientRect();
                  let x = rect.left - 32;
                  let y = -(window.innerHeight - rect.top - 22);
                  el.style.display = '';
                  el.style.webkitTransform = `translate3d(0,${y}px,0)`;
                  el.style.transform = `translate3d(0,${y}px,0)`;
                  let inner = el.getElementsByClassName('inner-hook')[0];
                  inner.style.webkitTransform = `translate3d(${x}px,0,0)`;
                  inner.style.transform = `translate3d(${x}px,0,0)`;
                }
              }
            },
            enter(el) {
              /* eslint-disable no-unused-vars */
              let rf = el.offsetHeight;
              this.$nextTick(() => {
                el.style.webkitTransform = 'translate3d(0,0,0)';
                el.style.transform = 'translate3d(0,0,0)';
                let inner = el.getElementsByClassName('inner-hook')[0];
                inner.style.webkitTransform = 'translate3d(0,0,0)';
                inner.style.transform = 'translate3d(0,0,0)';
              })
            },
            afterEnter (el) {
              let ball = this.dropBalls.shift();
              if(ball) {
                ball.show = false;
                el.style.display = 'none';
              }
            }
          }
        },
        empty () {
          this.selectFoods.forEach((food) => {
            food.count = 0;
          })
        },
        hideList () {
          this.fold = true;
        }
      },
      components : {
        cartcontrol
      }
    }
</script>

<style lang="less">
  @import "../../common/style/index";
  .shopcart {
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 50;
    width: 100%;
    height: 48px;
    background-color: #141D27;
    .content {
      display: flex;
      .content-left {
        flex: 1;
        font-size: 0;
        background-color: #141D27;
        .logo-wrapper {
          display: inline-block;
          position: relative;
          top: -10px;
          margin: 0 12px;
          padding: 6px;
          width: 56px;
          height: 56px;
          box-sizing: border-box;
          vertical-align: top;
          border-radius: 50%;
          background-color: #141D27;
          .logo {
            width: 100%;
            height: 100%;
            border-radius: 50%;
            text-align: center;
            background-color: #2B343C;
            &.highlight {
              background-color: rgb(0,160,220);
            }
            .icon-shopping_cart {
              font-size: 24px;
              line-height: 44px;
              color: #80858A;
              &.highlight {
                color: #fff;
              }
            }
          }
        }
        .num {
          position: absolute;
          top: 0;
          right: 0;
          width: 24px;
          height: 16px;
          line-height: 16px;
          text-align: center;
          border-radius: 16px;
          font-size: 9px;
          font-weight: 700;
          color: #fff;
          background: rgb(240,20,20);
          box-shadow: 0 4px 8px 0 rgba(0,0,0,0.4);
        }
        .price {
          display: inline-block;
          vertical-align: top;
          font-size: 16px;
          font-weight: 700;
          line-height: 24px;
          margin-top: 12px;
          padding-right: 12px;
          box-sizing: border-box;
          border-right: 1px solid rgba(255, 255, 255, 0.1);
          color: rgba(255,255,255,0.4);
          &.highlight {
            color: #fff;
          }
        }
        .desc {
          display: inline-block;
          vertical-align: top;
          line-height: 24px;
          margin: 12px 0 0 12px;
          color: rgba(255,255,255,0.4);
          font-size: 10px;
        }
      }
      .content-right {
        flex: 0 0 105px;
        width: 105px;
        background-color: #2b333b;
        .pay {
          font-size: 12px;
          color: rgba(255,255,255,0.4);
          font-weight: 700;
          height: 48px;
          line-height: 48px;
          text-align: center;
          &.not-enough {
            background-color: #2b333b;
          }
          &.enough {
            background: #00b43c;
            color: #fff;
          }
        }
      }
    }
    .ball-container {
      .ball {
        position: fixed;
        left: 32px;
        bottom: 22px;
        z-index: 200;
        &.drop-enter-active, &.drop-leave-active {
          transition: all .4s cubic-bezier(0.49,-0.29,0.75,0.41);
          .inner {
            width: 16px;
            height: 16px;
            border-radius: 50%;
            background-color: rgb(0,160,220);
            transition: all .4s linear;
          }
        }
      }
    }
    .shopcart-list {
      position: absolute;
      left: 0;
      top: 0;
      z-index: -10;
      width: 100%;
      transform: translate3d(0,-100%,0);
      &.fold-enter-active,&.fold-leave-active {
        transition: all .5s;
      }
      &.fold-enter,&.fold-leave-active {
        transform: translate3d(0,0,0);
      }
      .list-header {
        height: 40px;
        line-height: 40px;
        padding: 0 18px;
        background: #f3f5f7;
        border-bottom: 1px solid rgba(7, 17, 27, 0.1);
        .title {
          float: left;
          font-size: 14px;
          color: rgb(7,17,27);
        }
        .empty {
          float: right;
          font-size: 12px;
          color: rgb(0,160,220);
        }
      }
      .list-content {
        padding: 0 18px;
        max-height: 217px;
        overflow: hidden;
        background: #fff;
        .food {
          position: relative;
          padding: 12px 0;
          box-sizing: border-box;
          .border-1px(rgba(7, 17, 27, 0.1));
          .name {
            line-height: 24px;
            font-size: 14px;
            color: rgb(7,17,27);
          }
          .price {
            position: absolute;
            right: 90px;
            bottom: 12px;
            line-height: 24px;
            font-size: 14px;
            font-weight: 700;
            color: rgb(240,20,20);
          }
          .cartcontrol-wrapper {
            position: absolute;
            right: 0;
            bottom: 6px;
          }
        }
      }
    }
  }
  .list-mask {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 40;
    backdrop-filte: blur(10px);
    transition: all .5s;
    background: rgba(7,17,27,0.6);
    &.fade-enter-active,&.fade-leave-active {
      opacity: 1;
    &.fade-enter,&.fade-leave-active {
      opacity: 0;
      background: rgba(7,17,27,0);
    }
    }
  }
</style>



