<template>
  <div id="app">
    <!--顶部详情-->
    <v-header :seller="seller"></v-header>
    <!--商品详情-->
    <div class="tab border-1px">
       <div class="tab-item">
         <router-link to="/goods">商品</router-link>
       </div>
       <div class="tab-item">
         <router-link to="/ratings">评论</router-link>
       </div>
       <div class="tab-item">
         <router-link to="/seller">商家</router-link>
       </div>
    </div>
    <router-view :seller="seller"></router-view>
  </div>
</template>

<script>
import header from "@/components/header/header.vue";

const ERR_OK = 0;
export default {
  data () {
    return {
      seller : {}
    }
  },
  created() {
    let t = this;
    this.axios.get("/api/seller")
      .then(function (response) {
        if (response.data.errno === ERR_OK) {
          t.seller = response.data.data;
//          console.log(t.seller);
        }
      })
  },
  components:{
    "v-header" : header
  }
}
</script>

<style lang="less">
  @import "./common/style/index.less";
  #app {
    .tab {
      display: flex;
      width: 100%;
      height: 40px;
      line-height: 40px;
      .border-1px(rgba(7, 17, 27, 0.1));
      .tab-item {
        flex: 1;
        text-align: center;
        &>a{
          display: block;
          font-size: 14px;
          color: rgb(77,85,93);
          &.active{
            color: rgb(240,20,20);
          }
        }
      }
    }
  }
</style>



