import Vue from 'vue';
import App from './App';
import router from './router';
import axios from 'axios';

Vue.prototype.axios = axios;
// import "./common/style/index.less";
Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App)
});



