<!--商品模版-->
<template>
  <div>
    <div class="goods">
      <div class="menu-wrapper" ref="menuWrapper">
        <ul>
          <li v-for="(item,index) in goods" class="menu-item"
              :class="{'current':currentIndex === index}" @click="selectMenu(index,$event)">
            <span class="text border-1px">
              <span v-show="item.type > 0" class="icon" :class="classMap[item.type]"></span>{{item.name}}
            </span>
          </li>
        </ul>
      </div>
      <div class="foods-wrapper" ref="foodsWrapper">
        <ul>
          <li v-for="item in goods" class="food-list food-list-hook">
            <h1 class="title">{{item.name}}</h1>
            <ul>
              <li @click="selectFood(food,$event)" v-for="food in item.foods" class="food-item border-1px">
                <div class="icon">
                  <img width="57px" height="57px" :src="food.icon" alt="">
                </div>
                <div class="content">
                  <h2 class="name">{{food.name}}</h2>
                  <p class="desc">{{food.description}}</p>
                  <div class="extra">
                    <span class="count">月售{{food.sellCount}}份</span><span>好评率{{food.rating}}%</span>
                  </div>
                  <div class="price">
                    <span class="now">￥{{food.price}}</span><span class="old" v-show="food.oldPrice">{{food.oldPrice}}</span>
                  </div>
                  <div class="cartcontrol-wrapper">
                    <cartcontrol :food="food"></cartcontrol>
                  </div>
                </div>
              </li>
            </ul>
          </li>
        </ul>
      </div>
      <shopcart ref="shopcart" :delivery-price="seller.deliveryPrice"
                :select-foods="selectFoods" :min-price="seller.minPrice"></shopcart>
    </div>
    <food ref="food" :food="selectedFood"></food>
  </div>
</template>

<script>
  import BScroll from "better-scroll";
  import shopcart from "../shopcart/shopcart.vue";
  import cartcontrol from "../cartcontrol/cartcontroLvue.vue";
  import food from "../food/food.vue"
  const ERR_OK = 0;

  export default {
    props: {
      seller : {
        type : Object
      }
    },
    data() {
      return {
//        axios获得的数据
        goods : [],
//        li的高度数组
        listHeight : [],
//        检测一个实时滚动位子变量
        scrollY : 0,
        selectedFood : {}
      }
    },
    created () {
      this.classMap = ["decrease","discount","guarantee","invoice","special"];
      let t = this;
      this.axios.get("/api/goods")
        .then(function (response) {
          if (response.data.errno === ERR_OK) {
            t.goods = response.data.data;
//            获取了数据才能滑动
            t.$nextTick(() => {
              t.scroll();
              t._calculateHeight();
            });
          }
        });
    },
    computed : {
//      遍历li楼层高度
      currentIndex() {
        for (let i =0; i < this.listHeight.length; i++) {
          let height = this.listHeight[i];
          let height2 = this.listHeight[i+1];
//          判断位子，如果不等于后一楼，或大于等于当前楼层并且小于后一楼层，返回对应楼层索引值
          if (!height2 || (this.scrollY >= height && this.scrollY < height2)) {
              return i;
          }
        }
        return 0;
      },
      selectFoods () {
        let foods = [];
        this.goods.forEach((good) => {
          good.foods.forEach((food) => {
            if(food.count) {
              foods.push(food);
            }
          })
        })
        return foods;
      }
    },
    methods: {
      scroll() {
//        初始化better-scroll
        this.menuScrool = new BScroll(this.$refs.menuWrapper,{click : true});
//                                                           监测滚动的位子
        this.foodsScrool = new BScroll(this.$refs.foodsWrapper,{click : true,probeType : 3});
//                               pos返回x,y的值
        this.foodsScrool.on("scroll",(pos) => {
//          获取监听的位子
            this.scrollY = Math.abs(Math.round(pos.y));
//          console.log(this.scrollY);
        })
      },
      _drop(target) {
          this.$refs.shopcart.drop(target)
      },
      _calculateHeight() {
//        获取了li数组
//        let foodList = this.$refs.foodsWrapper.getElementsByClassName("food-list-hook");
        let foodList = document.getElementsByClassName("food-list-hook");
        let height = 0;
        this.listHeight.push(height);
//        遍历li数组
        for (let i = 0; i < foodList.length; i++) {
//          获得每个li的内容
          let item = foodList[i];
//          累加li的高度
          height += item.clientHeight;
//          添加每个li的可视高度进数组
          this.listHeight.push(height);
//          console.log(this.listHeight);

        }
      },
      selectMenu (index,event) {
        if (!event._constructed) { //去掉自带的click事件点击，即pc端直接返回
          return;
        }
//        获取li数组
        let foodsList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
//        指定点击的li
        let el = foodsList[index];
        //类似jump to的功能,通过这个方法,跳转到指定的dom
        this.foodsScrool.scrollToElement(el, 300);
      },
      selectFood (food,event) {
        if (!event._constructed) {
          return
        }
        this.selectedFood = food;
        this.$refs.food.show();
      }
    },
    components : {
      shopcart,
      cartcontrol,
      food
    },
    events : {
      'cart-add'(target) {
        this._drop(target)
      }
    }
  }
</script>

<style lang="less">
  @import "../../common/style/mixin";
  .goods {
    display: flex;
    position: absolute;
    top: 174px;
    bottom: 46px;
    width: 100%;
    overflow: hidden;
    .menu-wrapper {
      flex: 0 0 80px;
      width: 80px;
      background: #f3f5f7;
      .menu-item {
        display: table;
        width: 56px;
        height: 54px;
        line-height: 14px;
        padding: 0 12px;
        &.current {
          position: relative;
          z-index: 10;
          margin-top: -1px;
          background: #fff;
          font-weight: 700;
          .text {
            .border-none;
          }
        }
        .icon {
          display: inline-block;
          vertical-align: top;
          width: 12px;
          height: 12px;
          margin-right: 2px;
          background-size: 12px;
          background-repeat: no-repeat;
          &.decrease {
            .bg-image("../../components/goods/decrease_3");
          }
          &.discount {
            .bg-image("../../components/goods/discount_3");
          }
          &.guarantee {
            .bg-image("../../components/goods/guarantee_3");
          }
          &.invoice {
            .bg-image("../../components/goods/invoice_3");
          }
          &.special {
            .bg-image("../../components/goods/special_3");
          }
        }
        .text {
          display: table-cell;
          width: 56px;
          text-align: center;
          vertical-align: middle;
          font-size: 12px;
          color: #07111B;
          font-weight: 200;
          .border-1px(rgba(7,17,27,0.1));
        }
      }
    }
    .foods-wrapper {
      flex: 1;
      .title {
        padding-left: 14px;
        height: 26px;
        line-height: 26px;
        border-left: 2px solid #d9dde1;
        background-color: #f3f5f7;
        font-size: 12px;
        color: rgb(147,153,159);
      }
      .food-item {
        display: flex;
        margin: 18px 18px 0 18px;
        padding-bottom: 18px;
        .border-1px(rgba(7,17,27,0.1));
        &:last-child {
          .border-none;
        }
        .icon {
          flex: 0 0 57px;
          margin-right: 10px;
        }
        .content {
          flex: 1;
          .name {
            margin-top: 2px;
            font-size: 14px;
            line-height: 14px;
            color: rgb(7,17,27);
          }
          .desc,.extra {
            line-height: 10px;
            font-size: 10px;
            color: rgb(147,153,159);
          }
          .desc {
            margin: 8px 0;
          }
          .extra {
              .count {
                margin-right: 12px;
              }
          }
          .now {
            margin-right: 8px;
            font-size: 14px;
            font-weight: 700;
            line-height: 24px;
            color: rgb(240,20,20);
          }
          .old {
            text-decoration: line-through;
            font-size: 10px;
            line-height: 24px;
            color: rgb(147,153,159);
          }
          .cartcontrol-wrapper {
            position: absolute;
            right: 0;
            bottom: 12px;
          }
        }
      }
    }
  }
</style>



