
<!--头部模版-->

<template>
  <div class="header">
    <!--主体容器-->
    <div class="content-wrapper">
      <!--产品图片-->
      <div class="avatar">
        <img width="64" height="64" :src="seller.avatar">
      </div>
      <!--产品简介-->
      <div class="content">
        <div class="title">
          <span class="brand"></span>
          <span class="name">{{seller.name}}</span>
        </div>
        <!--送达方式/时间-->
        <div class="description">
          {{seller.description}}/{{seller.deliveryTime}}分钟送达
        </div>
        <!--优惠活动-->
        <div v-if="seller.supports" class="support" @click="showDetail">
          <span class="icon" :class="classMap[seller.supports[0].type]"></span>
          <span class="text">{{seller.supports[0].description}}</span>
        </div>
      </div>
      <!--更多优惠-->
      <div class="support-count" v-if="seller.supports" @click="showDetail">
          <span class="count">{{seller.supports.length+"个"}}</span>
          <i class="icon-keyboard_arrow_right"></i>
      </div>
    </div>
    <!--公告区-->
    <div class="bulletin-wrapper">
      <span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
      <i class="icon-keyboard_arrow_right"></i>
    </div>
    <!--背景图-->
    <div class="background">
      <img width="100%" height="100%" :src="seller.avatar">
    </div>
    <!--商品页-->
    <transition name="fade">
      <div class="detail" v-show="detailShow">
        <div class="detail-wrapper clearfix">
          <div class="detail-main">
            <h1 class="name">{{seller.name}}</h1>
            <div class="star-wrapper">
              <!--父传子值发送对应的图片大小和评分-->
              <star :size="48" :score="seller.score"></star>
            </div>
            <!--优惠信息-->
            <div class="title">
              <div class="line"></div>
              <div class="text">优惠信息</div>
              <div class="line"></div>
            </div>
            <ul v-if="seller.supports" class="supprts">
              <li class="support-item" v-for="(item,$index) in seller.supports">
                <span class="icon" :class="classMap[seller.supports[$index].type]"></span>
                <span class="text">{{item.description}}</span>
              </li>
            </ul>
            <div class="title">
              <div class="line"></div>
              <div class="text">商家公告</div>
              <div class="line"></div>
            </div>
            <p class="bulletin-text">{{seller.bulletin}}</p>
          </div>
        </div>
        <div class="detail-close">
          <i class="icon-close" @click="hideDetail"></i>
        </div>
      </div>
    </transition>
  </div>
</template>

<script type="text/ecmascript-6">
  import star from "../star/star.vue";
  export default {
    props : {
      seller: {
        type: Object
      }
    },
    data () {
      return {
        detailShow : false,
      }
    },
    created () {
      this.classMap = ["decrease","discount","guarantee","invoice","special"];
    },
    components : {
      star
    },
    methods : {
      showDetail : function () {
        this.detailShow = true;
      },
      hideDetail : function () {
        this.detailShow = false;
      }
    }
  };
</script>

<style lang="less">
  @import "../../common/style/index";
  .header{
    position: relative;
    color: #fff;
    font-size: 0;
    overflow: hidden;
    background-color: rgba(7,17,27,0.5);
    /*主体区*/
    .content-wrapper {
      position: relative;
      padding: 24px 12px 18px 24px;
      .avatar{
        display: inline-block;
        vertical-align: top;
        img {
          -webkit-border-radius: 2px;
          -moz-border-radius: 2px;
          border-radius: 2px;
        }
      }
      .content{
        display: inline-block;
        margin-left: 16px;
        font-size: 14px;
        .title {
          margin: 2px 0px 8px;
          .brand {
            display: inline-block;
            vertical-align: top;
            width: 30px;
            height: 18px;
            .bg-image("../../components/header/brand");
            -webkit-background-size: 30px 18px;
            background-size: 30px 18px;
            background-repeat: no-repeat;
          }
          .name {
            margin-left: 6px;
            font-size: 16px;
            font-weight: bold;
            line-height: 18px;
          }
        }
        .description {
          font-size: 12px;
          line-height: 12px;
          margin-bottom: 10px;
        }
        .support {
          font-size: 0;
          .icon {
            display: inline-block;
            width: 12px;
            height: 12px;
            line-height: 12px;
            margin-right: 4px;
            -webkit-background-size: 12px;
            background-size: 12px;
            background-repeat: no-repeat;
            vertical-align: top;
            &.decrease {
                .bg-image("../../components/header/decrease_1");
              }
            &.discount {
              .bg-image("../../components/header/discount_1");
            }
            &.guarantee {
              .bg-image("../../components/header/guarantee_1");
            }
            &.invoice {
              .bg-image("../../components/header/invoice_1");
            }
            &.special {
              .bg-image("../../components/header/special_1");
            }
          }
          .text {
            font-size: 12px;
            line-height: 12px;
            /*vertical-align: middle;*/
          }
        }
      }
      .support-count {
        position: absolute;
        right: 12px;
        bottom: 18px;
        height: 24px;
        padding: 0 8px;
        line-height: 24px;
        border-radius: 14px;
        background-color: rgba(0,0,0,0.2);
        text-align: center;
        .count {
          font-size: 12px;
          line-height: 24px;
        }
        .icon-keyboard_arrow_right {
          margin-left: 2px;
          font-size: 10px;
          font-weight: 200;
        }
      }
    }
    /*公告区*/
    .bulletin-wrapper {
      position: relative;
      height: 28px;
      line-height: 28px;
      padding: 0 22px 0 12px;
      background-color: rgba(7,17,27,0.1);
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      font-size: 10px;
      .bulletin-title {
        display: inline-block;
        width: 22px;
        height: 12px;
        /*font-size: 0;*/
        vertical-align: middle;
        .bg-image("../../components/header/bulletin");
        background-size: 22px 12px;
        background-repeat: no-repeat;
      }
      .bulletin-text {
        vertical-align: top;
        margin: 0 4px;
        font-size: 10px;
      }
      .icon-keyboard_arrow_right {
        position: absolute;
        right: 12px;
        top: 9px;
        font-size: 10px;
      }
    }
    /*背景图*/
    .background {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
      filter: blur(10px);
    }
    /*商品详情*/
    .detail {
      position: fixed;
      z-index: 100;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      overflow: auto;
      transition: all .5s;
      background: rgba(7,17,27,0.8);
      &.fade-enter-active {
        opacity: 1;
      }
      &.fade-enter, &.fade-leave-active {
        opacity: 0;
      }
      .detail-wrapper {
        min-height: 100%;
        .detail-main {
          padding-top: 64px;
          padding-bottom: 64px;
          .name {
            text-align: center;
            font-size: 16px;
            font-weight: 700;
            line-height: 16px;
          }
        }
        .star-wrapper {
          text-align: center;
          margin-top: 16px;
        }
        .title {
          display: flex;
          width: 80%;
          margin: 28px auto 24px;
          font-size: 14px;
          .line {
            position: relative;
            top: 7px;
            flex: 1;
            height: 2px;
            background-color: rgba(255,255,255,0.2);
          }
          .text {
            padding: 0 12px;
            font-weight: 700;
            font-size: 14px;
          }
        }
        .supprts {
          width: 80%;
          margin: 0 auto;
          .support-item {
            padding: 0 12px;
            margin-bottom: 12px;
            &:last-child {
              margin-bottom: 0;
            }
            .icon {
              display: inline-block;
              width: 16px;
              height: 16px;
              vertical-align: top;
              margin-right: 6px;
              background-size: 16px;
              background-repeat: no-repeat;
              &.decrease {
                .bg-image("../../components/header/decrease_2");
              }
              &.discount {
                .bg-image("../../components/header/discount_2");
              }
              &.guarantee {
                .bg-image("../../components/header/guarantee_2");
              }
              &.invoice {
                .bg-image("../../components/header/invoice_2");
              }
              &.special {
                .bg-image("../../components/header/special_2");
              }
            }
            .text {
              font-size: 12px;
              line-height: 16px;
            }
          }
        }
        .bulletin-text {
          width: 80%;
          margin: 0 auto;
          padding: 0 12px;
          font-size: 12px;
          font-weight: 200;
          line-height: 24px;
        }
      }
      /*关闭商品详情*/
      .detail-close {
        position: relative;
        width: 32px;
        height: 32px;
        margin: -64px auto 0;
        clear: both;
        font-size: 32px;
        color: rgba(255,255,255,0.5);
      }
    }
  }
</style>



